@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.p-todo{
    border: solid black 1px;
    text-align: center;
    width: 700px;
    margin: 0 auto;
    background-image: linear-gradient(to right, #87CEEB , #71FD9B);
    color: white;
    margin-top: 50px;
}


.p-todo input{
    width: 40%;
    margin: 20px auto;
    display: inline-block;
    background-image: linear-gradient(to right, #87CEEB , #71FD9B);
    border: 2px black solid;

}

.p-todo h1{
    margin-top: 20px;
}

.p-todo h5{
    color: grey;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}



@media screen and (max-width: 600px) {
    
    .p-todo{
        width: 100%; 
        margin:none;
        border: none;
        height: -webkit-max-content;
        height: max-content;
        margin-top: 0 !important;
    }


    .p-todo input{
        width: 90%;
        margin: 0;
    }

       

    .p-todo h5{
        margin-top: 49px;
        margin-right: 19px;
        font-size: 12px;
        color: black;
        font-weight: bold;
    }
  }

.c-rowtask{
}

tr.border_bottom td  {
    border-bottom: 1px solid lightgrey;
    border-top: none !important;
    text-align: left;
    vertical-align: 0% !important;
    font-size: 20px;
  

  }

  .check{
      width: 10% !important;
  }

    input[type='checkbox'] {
        width: 1.1rem;
        height: 1.1rem;
        margin: 0;
        vertical-align: middle;
        cursor: pointer;
     }

     .value{
         margin-bottom: 10px;
     }

    

      .icon{
          text-align: center !important;
          display: none; 
         
      }

      #todo-table tr:hover .icon{
        display: inline;
        color: black;
      }

      .iconWarper{
          text-align: center !important;
      }

      svg{
        cursor: pointer;
        float: right
      }

      .complete{
        color: lightgrey;
        text-decoration: line-through;
        font-weight: normal;

      }


      @media screen and (max-width: 600px) {
        .c-rowtask{
          position:absolute;
          top:120px;
          bottom:100px;
          left:0px;
          right:0px;
          overflow:auto;
        
        }

      }
.c-sortsection{
    padding: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background-color: black;
}

.c-sortsection button{
    margin-right: 10px;
}




@media screen and (max-width: 600px) {
    .c-sortsection{
        position:absolute;
        bottom:0px;
        height:100px;
        left:0px;
        right:0px;
        overflow:hidden
    }
    

    .bold{
        margin-top: 5px;
    }

    .button-section{
        margin-left: 22px;

    }
  }
.App{
  font-family: 'Anton', sans-serif;
  background-color: black !important;
}

@media screen and (max-width: 600px) {
.App{
  height: calc(100vh -  50px);
  background-image: linear-gradient(to right, #87CEEB , #71FD9B);

}

}
